import Component from '../react/component'

import { diff, diffNode } from './diff'

const ReactDOM = {
  render
}

// åˆ›å»ºè‡ªå®šä¹‰ç»„ä»¶
// vnode.tag, vnode.attrs
export function createComponent (comp, props) {
  let inst
  // åŸå‹é“¾éç©ºï¼Œè¿˜æœ‰renderæ–¹æ³•ï¼Œé‚£ä¹ˆä¸€å®šæ˜¯ç±»ç»„ä»¶
  if (comp.prototype && comp.prototype.render) {
    // console.log("props", props)
    inst = new comp(props)
    // console.log("inst" ,inst)
  } else {
    // å¦‚æœä¸æ˜¯ç±»ç»„ä»¶ï¼Œæˆ‘ä»¬å°±ä¹Ÿæ˜¯ç”¨ç±»ç»„ä»¶åˆ›å»º
    inst = new Component(props)
    // å°†æ„é€ å‡½æ•°èµ‹å€¼ï¼Œå‡½æ•°ç»„ä»¶åŸæ¥çš„tagå°±æ˜¯å‡½æ•°æœ¬èº«ï¼Œå› æ­¤ç”¨äºæ„é€ å‡½æ•°ååˆ†åˆ‡åˆ
    inst.constructor = comp
    // æ¯ä¸€ä¸ªç±»ç»„ä»¶éƒ½æœ‰ä¸€ä¸ªrenderæ–¹æ³•ï¼Œå¯¹è¯¥renderè¿›è¡Œèµ‹å€¼ç»™å…¶ä»–å‡½æ•°ï¼Œå¹¶ä¸”æ˜¯è¿”å›ä¸€ä¸ªæ„é€ å‡½æ•°å†…éƒ¨çš„jsx
    inst.render = function () {
      return this.constructor(props)
    }
  }
  return inst
}

// è¿™æ˜¯ä¸ºäº†ç»™compå†…éƒ¨æ·»åŠ baseå¯¹è±¡è€Œè®¾ç½®çš„å‡½æ•°
// ç»„ä»¶æ›´æ–°å¯ä»¥è®¾ç½®ç”Ÿå‘½å‘¨æœŸ
export function renderComponent (comp) {
  // å®šä¹‰ç»„ä»¶å†…éƒ¨çš„èŠ‚ç‚¹å¯¹è±¡
  let base

  // æ‹¿åˆ°äº†å…ƒç´ 
  const renderer = comp.render()
  // console.log(renderer)

  if (comp.base && componentWillUpdate) {
    comp.componentWillUpdate()
  }

  // rendereræ˜¯è·å–äº†ç±»ç»„ä»¶å†…éƒ¨çš„å…ƒç´ ï¼Œä½†è¿˜æ˜¯éœ€è¦ä¸€å±‚_render()å‡½æ•°è§£æï¼Œä¸ç„¶è¿˜æ˜¯æ— æ³•è§£æ
  // base = _render(renderer)
  console.log("comp", comp)
  console.log("renderer", renderer)
  base = diffNode(comp.childNodes, renderer)

  if (comp.base) {
    if (comp.componentDidUpdate) {
      comp.componentDidUpdate()
    }
  } else if (comp.componentDidMount) {
    comp.componentDidMount()
  }

  // if (comp.base && comp.base.parentNode) {
  //   // replaceChildæ˜¯åªèƒ½ç”¨äºå­ç»„ä»¶ï¼Œå› æ­¤æˆ‘ä»¬å¿…é¡»ä½¿ç”¨parentNode
  //   // å°†baseèµ‹å€¼ç»™comp.base
  //   comp.base.parentNode.replaceChild(base, comp.base)
  // }
  comp.base = base
}

export function setComponentProps (comp, props) {
  if (!comp.base) {
    if (comp.componentWillMount) {
      comp.componentWillMount()
    }
    if (comp.componentWillReceiveProps) {
      comp.componentWillReceiveProps(props)
    }
  }
  // è®¾ç½®ç»„ä»¶çš„å±æ€§
  comp.props = props
  // æ¸²æŸ“ç»„ä»¶
  renderComponent(comp)
}

function _render (vnode) {
  if (vnode === undefined || vnode === null || typeof vnode === 'boolean') vnode = ''
  if (typeof vnode === 'number') {
    vnode = String(vnode)
  }
  if (typeof vnode === 'string') {
    return document.createTextNode(vnode)
  }

  // åˆ¤æ–­æ˜¯å¦ä¸ºå‡½æ•°ç»„ä»¶æˆ–ç±»ç»„ä»¶ã€æ ¹æ®tagï¼Œä¹Ÿå°±æ˜¯å½“å‰ç»„ä»¶æ¥åˆ†æã€‘
  // å¿…é¡»åœ¨renderä¸‹æ‰èƒ½å°†attrså˜æˆprops
  if (typeof vnode.tag === 'function') {
    // 1.åˆ›å»ºç»„ä»¶
    const comp = createComponent(vnode.tag, vnode.attrs)
    // 2.è®¾ç½®ç»„ä»¶çš„å±æ€§ï¼Œåœ¨è¿™é‡Œï¼Œæ‰€æœ‰çš„å±æ€§éƒ½åœ¨è¿™é‡Œå˜æˆäº†props
    setComponentProps(comp, vnode.attrs)
    // 3.ç»„ä»¶æ¸²æŸ“çš„èŠ‚ç‚¹å¯¹è±¡è¿”å›
    return comp.base
  }

  const { tag, attrs, childrens } = vnode
  const dom = document.createElement(tag)

  if (attrs) {
    Object.keys(attrs).forEach(key => {
      const value = attrs[key]
      setAttribute(dom, key, value)
    })
  }

  if (childrens?.length > 0) {
    for (let item of childrens) {
      render(item, dom)
    }
  }
  return dom
}

function render (vnode, container, dom) {
  return diff(dom, vnode, container)
  // return container.appendChild(_render(vnode))
}

// è®¾ç½®å±æ€§ã€valueä¸ºkeyå¯¹åº”çš„é”®å€¼ã€‘
export function setAttribute (dom, key, value) {
  // å°†å±æ€§åçš„classNameè½¬æ¢æˆclass
  if (key === 'className') {
    key = 'class'
  }

  // è½¬æ¢äº‹ä»¶
  if (/on\w+/.test(key)) {
    key = key.toLowerCase()
    dom[key] = value || ''
  } else if (key === 'style') {
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '' 
    } else if (value && typeof value === 'object') {
      for(let k in value) {
        if (typeof value[k] === 'number') {
          dom.style[k] = value[k] + 'px'
        } else {
          dom.style[k] = value[k]
        }
      }
    }
  } else {
    if (key in dom) {
      dom[key] = value || ''
    }

    if (value) {
      dom.setAttribute(key, value)
    } else {
      dom.removeAttribute(key)
    }
  }
}

export default ReactDOM